import { request } from '@/utils/request';
import type { IResponse, IListResp } from '@/types/response';
import type { GetSamplesParams } from '@/api/sample';

const api = {
  list: '/datasets/list',
  create: '/datasets/create',
  update: '/datasets/update',
  delete: '/datasets/delete',
  batchDelete: '/datasets/batch_delete',
  export: '/datasets/export',
  bindSamplesByName: '/datasets/bind_samples_by_names',
  bindSamplesByFilter: '/datasets/create_filter',
  unbindSample: '/datasets/unbind_sample',
  divideDataset: '/datasets/split',
  packageDataset: '/datasets/package',
  downloadDataset: '/datasets/download',
  checkUsage: '/datasets/check_usage'
};

// 数据集接口参数类型定义
export interface Dataset {
  id: number;
  name: string;
  description?: string;
  project_id: number;
  sample_count: number;
  username: string;
  created_at: string;
  updated_at: string;
}

export interface GetDatasetsParams {
  project_id: number;
  name?: string;
  page?: number;
  size?: number;
  forceRefresh?: boolean;
}

export interface CreateDatasetParams {
  name: string;
  description?: string;
  project_id: number | string;
}

export interface UpdateDatasetParams {
  id: number | string;
  name: string;
  description?: string;
  project_id: number | string;
}

export interface DeleteDatasetParams {
  dataset_id: number | string;
  project_id: number | string;
}

export interface DeleteBatchDatasetsParams {
  dataset_ids: number[];
  project_id: number | string;
}

export interface ExportDatasetParams {
  dataset_id: number;
  format: 'json' | 'csv' | 'xml';
}

// 添加样本到数据集参数类型
export interface AddSamplesToDatasetParams {
  dataset_name: string;
  sample_ids: number[];
  project_id: number | string;
}

/**
 * 获取数据集列表
 * @param params 查询参数
 * @returns 数据集列表
 */
export function getDatasets(params: GetDatasetsParams) {
  return request.get<IResponse<IListResp<Dataset>>>(api.list, { params });
}

/**
 * 创建数据集
 * @param data 创建数据集参数
 * @returns 创建的数据集
 */
export function createDataset(data: CreateDatasetParams) {
  return request.post<IResponse<Dataset>>(api.create, data);
}

/**
 * 更新数据集
 * @param data 更新数据集参数
 * @returns 更新的数据集
 */
export function updateDataset(data: UpdateDatasetParams) {
  return request.patch<IResponse<Dataset>>(api.update, data);
}

/**
 * 删除数据集
 * @param data 删除数据集参数
 * @returns 删除结果
 */
export function deleteDataset(data: DeleteDatasetParams) {
  return request.post<IResponse<{ success: boolean }>>(api.delete, data);
}

/**
 * 批量删除数据集
 * @param data 批量删除数据集参数
 * @returns 删除结果
 */
export function deleteBatchDatasets(data: DeleteBatchDatasetsParams) {
  return request.post<IResponse<{ success: boolean }>>(api.batchDelete, data);
}

/**
 * 导出数据集
 * @param params 导出数据集参数
 * @returns 导出结果
 */
export function exportDataset(params: ExportDatasetParams) {
  return request.get(api.export, { 
    params,
    responseType: 'blob' // 设置响应类型为blob，用于文件下载
  });
}

/**
 * 添加样本到数据集
 * @param data 添加样本到数据集参数
 * @returns 添加结果
 */
export function bindSamplesByName(data: AddSamplesToDatasetParams) {
  return request.post<IResponse<{ success: boolean }>>(api.bindSamplesByName, data);
} 

/**
 * 根据筛选条件添加样本到数据集
 * @param data 创建过滤器参数
 * @returns 创建结果
 */
export interface CreateFilterParams {
  project_id: number;  // 项目ID，必填，大于0
  dataset_name: string;  // 数据集名称，必填，长度1-100
  status?: number | string;  // 样本状态：1-未处理，2-背景，3-已标注，可选
  id?: number | string;  // 样本ID，可选
  name?: string;  // 样本名称，支持模糊搜索，可选
  creator_name?: string;  // 创建人名称，支持模糊搜索，可选
  label_ids?: number[];  // 类别ID列表，可选
  tag_ids?: number[];  // 标签ID列表（保留兼容性），可选
  tag_filter_mode?: 'or' | 'and' | 'not';  // 标签筛选模式：或、与、非（保留兼容性），可选
  use_tag_groups?: boolean;  // 是否使用标签组模式，可选
  tag_groups?: string;  // 标签组（字符串格式），可选
  tag_groups_logic?: 'and' | 'or';  // 标签组间逻辑，可选
  dataset_ids?: number[];  // 数据集ID列表，可选
  sort?: string;  // 排序方式，可选
  unSelectedIds?: number[];  // 需要排除的样本ID列表，可选
}

export function bindSamplesByFilter(data: CreateFilterParams) {
  return request.post<IResponse<{ success: boolean }>>(api.bindSamplesByFilter, data);
}

/**
 * 从数据集中移除样本
 * @param data 移除样本参数
 * @returns 移除结果
 */
export interface UnbindSampleParams {
  dataset_id: number;  // 数据集ID，必填
  sample_id: number;  // 样本ID，必填
}

export function unbindSample(data: UnbindSampleParams) {
  return request.post<IResponse<{ success: boolean }>>(api.unbindSample, data);
}

/**
 * 划分数据集
 * @param data 划分数据集参数
 * @returns 划分结果
 */
export interface DivideDatasetParams extends GetSamplesParams {
  train_dataset_name: string;  // 训练集名称，必填，长度1-100
  train_dataset_id: number | null;  // 训练集ID，选填，大于0
  test_dataset_name: string;  // 测试集名称，必填，长度1-100
  test_dataset_id: number | null;  // 测试集ID，选填，大于0
  test_ratio: number;  // 划分比例，必填，0-100
}

export function divideDataset(data: DivideDatasetParams) {
  return request.post<IResponse<{ success: boolean }>>(api.divideDataset, data);
}

/**
 * 打包数据集
 * @param data 打包数据集参数
 * @returns 打包结果
 */
export interface PackageDatasetParams {
  dataset_id: number;
  project_id: number;
}

export function packageDataset(data: PackageDatasetParams) {
  return request.post<IResponse<{ success: boolean }>>(api.packageDataset, data);
}

/**
 * 下载数据集
 * @param data 下载数据集参数
 * @returns 下载结果
 */
export interface DownloadDatasetParams {
  dataset_id: number;
}

export function downloadDataset(data: DownloadDatasetParams) {
  return request.get(`${api.downloadDataset}/${data.dataset_id}`, { responseType: 'blob' });
}

/**
 * 检查数据集使用状态
 * @param data 检查数据集使用状态参数
 * @returns 使用状态结果
 */
export interface CheckDatasetUsageParams {
  project_id: number;
  dataset_ids: number[];
}

export interface CheckDatasetUsageResponse {
  in_use: number[];
}

export function checkDatasetUsage(data: CheckDatasetUsageParams) {
  return request.post<IResponse<CheckDatasetUsageResponse>>(api.checkUsage, data);
}

